@import "../../../styles/vars";
@import "../../../styles/mixins";

.homeAdvertisement {
  width: 100%;
  background-image: url("../../../assets/images/home/HomeAdvertisement/homeAdvertisementBg.png");
  background-size: cover;

  &__container {
    padding-top: 52px;
    padding-bottom: 123px;
    @include flexCol(center, flex-start);
    gap: 70px;

    @include xxlScreenMedia {
      padding-bottom: 107px;
    }

    @include xlScreenMedia {
      padding-top: 44px;
      padding-bottom: 80px;
      gap: 36px;
    }

    @include lgScreenMedia {
      padding-top: 52px;
      padding-bottom: 67px;
    }

    @include smScreenMedia {
      gap: 0;
    }
  }

  &__texts {
    @include flexCol(flex-start, flex-start);
    flex-direction: column;
    width: 100%;
    color: $color-black;


    @include smScreenMedia {
      position: absolute;
      padding-left: 14px;
      padding-right: 14px;
    }

    &_active {
      @include smScreenMedia {
        animation: textUnDisplay .5s linear 2.5s 1 forwards;
      }
    }
  }

  &__text {
    @include setFont(192px, 400);
    line-height: 221px;
    white-space: nowrap;
    max-height: 0;
    overflow: hidden;

    @media (max-width: 1600px) {
      line-height: 188px;
      font-size: 185px;
    }

    @include xxlScreenMedia {
      font-size: 146px;
      line-height: 167px;
    }

    @include xlScreenMedia {
      font-size: 128px;
      line-height: 147px;
    }

    @media (max-width: 1100px) {
      line-height: 120px;
      font-size: 135px;
    }

    @include lgScreenMedia {
      font-size: 108px;
      line-height: 124px;
    }

    @media (max-width: 990px) {
      line-height: 115px;
      font-size: 100px;
    }

    @media (max-width: 920px) {
      line-height: 100px;
      font-size: 90px;
    }

    @media (max-width: 830px) {
      line-height: 90px;
      font-size: 80px;
    }

    @media (max-width: 700px) {
      line-height: 80px;
      font-size: 70px;
    }

    @media (max-width: 630px) {
      line-height: 70px;
      font-size: 60px;
    }

    @include smScreenMedia {
      font-size: 52px;
      line-height: 59px;
    }

    &_active {
      @media (min-width: $sm-screen) {
        animation: textDisplay .5s linear 0s 1 forwards;
      }
    }

    &_activeMob {
      @include smScreenMedia {
        animation: textDisplay .5s linear 0s 1 forwards;
        &:nth-child(2) {
          animation-delay: .5s;
        }

        &:nth-child(3) {
          animation-delay: 1s;
        }

        &:nth-child(4) {
          animation-delay: 1.5s;
        }

        &:nth-child(5) {
          animation-delay: 2s;
        }
      }
    }
  }

  &__images {
    @include flexCol();
    flex-direction: column;
    width: 100%;
    overflow: hidden;
  }

  &__img {
    width: 100%;
    opacity: 0;

    &_active {
      &:nth-child(1) {
        @media (min-width: $sm-screen) {
          animation: image1Display .5s linear 1 forwards;
        }
      }

      &:nth-child(2) {
        @media (min-width: $sm-screen) {
          animation: image2Display .5s linear 1 forwards;
        }
      }

      &:nth-child(3) {
        @media (min-width: $sm-screen) {
          animation: image3Display .5s linear 1 forwards;
        }
      }
    }

    &_activeMob {
      &:nth-child(1) {
        @include smScreenMedia {
          animation: image1Display .5s linear 2.5s 1 forwards;
        }
      }

      &:nth-child(2) {
        @include smScreenMedia {
          animation: image2Display .5s linear 3s 1 forwards;
        }
      }

      &:nth-child(3) {
        @include smScreenMedia {
          animation: image3Display .5s linear 3.5s 1 forwards;
        }
      }
    }
  }
}

@keyframes textUnDisplay {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes textDisplay {
  from {
    max-height: 0
  }
  to {
    max-height: 200px
  }
}

@keyframes image1Display {
  from {
    opacity: 0;
    transform: translateY(-40px);

  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


@keyframes image2Display {
  from {
    opacity: 0;
    transform: translateX(40px);

  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes image3Display {
  from {
    opacity: 0;
    transform: translateY(40px);

  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}